.progress {
    padding: 0;
    width: 25%;
    height: 30px;
    overflow: hidden;
    background: #e5e5e5;
    display: inline-block;
}

.barL {
    position: relative;
    float: left;
    min-width: 1%;
    height: 100%;
    background: #66bb6a;
}

.barR {
    position: relative;
    float: right;
    min-width: 1%;
    height: 100%;
    background: #ffa726;
}

.percent {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    font-size: 12px;
    color: white;
}

